.App {
  margin-top:0;
  margin-left: 0;
  margin-right: 0;
  margin-bottom: 0;
  padding-bottom: 5%;
  position: absolute;
    top: 0;
    left: 0;
    right: 0;
}

.dropdownContent {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-in;
  margin-bottom: 5%;
}

.dropdownContainer:hover > .dropdownContent {
