/* typography */

body {
  background-color: #F2E5D9;
  font-size: 16px;
}

h1 {
  font-family: "Fraunces", serif;
  font-optical-sizing: auto;
  font-weight: 680;
  font-style: normal;
  font-variation-settings:
    "SOFT" 100,
    "WONK" 0.5;
  color: #5C5347;
  font-size: clamp(2.5rem, 3vw, 9rem);
  margin-top: 0;
  margin-bottom: 3%;
}

h2 {
  -webkit-margin-before: 0;
          margin-block-start: 0;
  font-family: "Manrope", sans-serif;
  font-weight: 800;
  font-size: clamp(1.5rem, 1.15vw, 2.25rem);
  color: #382F2F;
  text-decoration: none;
  margin-bottom: 2.5%;
}

h2.contact {
  font-family: "Manrope", sans-serif;
  font-weight: 600;
  font-size: clamp(0.8rem, 1.25vw, 1.5rem);
  color: #382F2F;
  text-transform: uppercase;
}

p {
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-size: clamp(1rem, 1vw, 1.75rem);
  color: #382F2F;
}

.p-feature {
  font-family: "Fraunces", serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  font-variation-settings:
    "SOFT" 100,
    "WONK" 0.5;
  color: #5C5347;
  font-size: clamp(1.75rem, 3.5vw, 6.5rem);
}

.p-medium {
  font-family: "Manrope", sans-serif;
  font-weight: 600;
  font-size: clamp(1rem, 1.7vw, 2.5rem);
  color: #382F2F;
  text-decoration: none;
  margin-bottom: 5%;
}

.p-attribution {
  font-family: "Manrope", sans-serif;
    font-weight: 400;
    font-size: clamp(0.8rem, 0.6vw, 1.25rem);
    color: #382F2F;
    text-decoration: none;
}

a {
  -webkit-text-decoration:underline 1px solid;
          text-decoration:underline 1px solid;
  text-underline-offset: 5px;
  font-family: "Manrope", sans-serif;
  font-weight: 700;
  font-size: clamp(0.9rem, 1vw, 2rem);
  color: #382F2F;
}

.large-link:has(button) {
  text-decoration: none;
}

a:hover {
  transform: scale(1.1);
  transition: all 0.33s ease-in-out;
  -webkit-text-decoration-style: wavy;
          text-decoration-style: wavy;
}

.large-link {
  font-family: "Manrope", sans-serif;
  font-weight: 700;
  font-size: clamp(1.25rem, 1vw, 2rem);
  color: #382F2F;
  text-decoration: underline;
  text-underline-offset: 15px;
}

button {
  font-family: "Manrope", sans-serif;
    font-weight: 600;
    font-size: clamp(1rem, 1vw, 1.75rem);
    color: #382F2F;
    text-decoration: none;
    padding-top:0.5rem;
    padding-bottom:0.5rem;
    /* padding-left: 3rem;
    padding-right: 3rem; */
    border: 1.5px solid #382F2F;
    border-radius: 45px;
    box-shadow: 2px 2px 4px #5c534766;
    background-color: #F2EFEB;
    text-align: center;
}

ul {
	position: relative;
	list-style-type: circle;
	margin-left: 0;
	padding-left: 1.2rem;
}

li {
  font-family: "Manrope", sans-serif;
  font-weight: 600;
  font-size: clamp(1rem, 1vw, 1.75rem);
  color: #382F2F;
  text-decoration: none;  
  padding-left: 10px;
}

span {
  font-family: "Manrope", sans-serif;
  font-weight: 600;
  font-size: clamp(1rem, 1vw, 1.75rem);
  color: #382F2F;
  text-decoration: none;
}

span.project-label-large {
  font-family: "Manrope", sans-serif;
  font-weight: 600;
  font-size: clamp(1rem, 1.2vw, 2.25rem);
