.pi-process-container {
    max-width: 100%;
    background-color: #f2f2f2;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-row-gap: 15px;
    row-gap: 15px;
    align-items: top;
    justify-content: center;
    justify-items: center;
    align-content: center;
    padding-top: 2%;
    padding-bottom: 2%;
    padding-left: 0.5%;
    padding-right: 0.5%;
}

.mat-process-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-row-gap: 15px;
    row-gap: 15px;
    align-items: top;
    justify-content: center;
    justify-items: center;
    align-content: center;
    background-color: #f2f2f2;
    padding-top: 15px;
    padding-bottom: 15px;
    align-items: center;
    justify-content: center;
    justify-items: center;
    align-content: center;
    margin-bottom: 5%;
}

.app-process-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    grid-row-gap: 15px;
    row-gap: 15px;
    align-items: top;
    justify-content: center;
    justify-items: center;
    align-content: center;
    background-color: #f2f2f2;
    padding-top: 2%;
    padding-bottom: 2%;
    grid-row-gap: 15px;
    row-gap: 15px;
    align-items: center;
    justify-content: center;
    justify-items: center;
    align-content: center;
    max-width: 95%;
    margin-left: auto;
    margin-right: auto;
}

.rm-reqs-cont {
